'use client'

import Link from 'next/link';
import { useState } from 'react';

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <header className="sticky top-0 z-50 w-full border-b border-gray-200 bg-white/80 backdrop-blur-sm">
      <div className="container mx-auto px-4 flex justify-between items-center h-16">
        {/* Mobile Menu and Logo */}
        <div className="flex items-center gap-4">
          {/* Mobile Dropdown */}
          <div className="lg:hidden">
            <button 
              className="p-2 text-gray-600 hover:text-gray-900 transition-colors"
              onClick={() => setIsOpen(!isOpen)}
            >
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              </svg>
            </button>
            {isOpen && (
              <div className="absolute left-0 top-16 w-full bg-white border-b border-gray-200 shadow-lg">
                <nav className="container mx-auto px-4 py-2">
                  <ul className="space-y-2">
                    <li><Link href="/" className="block py-2 text-gray-600 hover:text-gray-900">Blog</Link></li>
                    <li><Link href="/music" className="block py-2 text-gray-600 hover:text-gray-900">Music</Link></li>
                    <li><Link href="/videos" className="block py-2 text-gray-600 hover:text-gray-900">Videos</Link></li>
                    <li><Link href="/photos" className="block py-2 text-gray-600 hover:text-gray-900">Photos</Link></li>
                    <li><Link href="/writings" className="block py-2 text-gray-600 hover:text-gray-900">Writings</Link></li>
                  </ul>
                </nav>
              </div>
            )}
          </div>

          {/* Logo/Brand */}
          <Link 
            href="/" 
            className="text-xl font-medium text-gray-900 hover:text-gray-600 transition-colors"
          >
            Felix Arash
          </Link>
        </div>

        {/* Desktop Menu */}
        <nav className="hidden lg:block">
          <ul className="flex items-center gap-8">
            <li>
              <Link href="/" className="text-gray-600 hover:text-gray-900 transition-colors">
                Blog
              </Link>
            </li>
            <li>
              <Link href="/music" className="text-gray-600 hover:text-gray-900 transition-colors">
                Music
              </Link>
            </li>
            <li>
              <Link href="/videos" className="text-gray-600 hover:text-gray-900 transition-colors">
                Videos
              </Link>
            </li>
            <li>
              <Link href="/photos" className="text-gray-600 hover:text-gray-900 transition-colors">
                Photos
              </Link>
            </li>
            <li>
              <Link href="/writings" className="text-gray-600 hover:text-gray-900 transition-colors">
                Writings
              </Link>
            </li>
          </ul>
        </nav>
      </div>
    </header>
  );
}